import React, { memo, useState } from 'react'
import { SearchOutlined } from "@ant-design/icons"
import { Collapse } from "antd"

import { SearchWrapper } from './style'
import { menus } from './data';
import { useDispatch } from 'react-redux';
import { getSearchListAction } from '@/store/modules/products';

const items = menus;

const ProductSearch = memo((props) => {

  const latest = props.latest
  const [inpValue, setInpValue] = useState('')

  const dispatch = useDispatch()
  // 搜索产品
  const handleSearchList = () => {
    dispatch(getSearchListAction(inpValue))
  }
  return (
    <SearchWrapper>
      <div className="search_top_inp">
        <input type="text" placeholder="Search" value={inpValue} onChange={e => setInpValue(e.target.value)} />
        <p className="search_btn" onClick={handleSearchList}>
          <SearchOutlined />
        </p>
      </div>
      <div className="product_categories">
        <p className="sear_prod_title">PRODUCT CATEGORIES</p>
        <div className="collapse">
          <Collapse items={items} />
        </div>
      </div>
      <div className="latest_products">
        <p className="sear_prod_title">LATEST PRODUCTS</p>
        <ul className="latest_three_items">
          {
            latest.map((item, index) => {
              return(
                <li key={index}>
                  <img src={require("@/assets/data/" + item.img)} alt="" />
                  <div className="latest_text">
                    <div className="latest_title">{item.title}</div>
                    <p>READ MORE</p>
                  </div>
                </li>
              )
            })
          }
        </ul>
      </div>
    </SearchWrapper>
  )
})

export default ProductSearch