import React, { memo, useEffect, useState } from 'react'

import { Card } from "antd"
import {ListWrapper} from "./style"
import Pagination from '../product-pagination'
import { useNavigate } from 'react-router-dom'
import { capitalizeWords } from "@/utils"

const ProductList = memo((props) => {
  const {products, parentUrl, title} = props

  // 总页数
  const totalPage = Math.floor(products.length/12) + 1
  // 初始化列表
  const [currentList, setCurrentList] = useState([])
  useEffect(() => {
    setCurrentList(products.slice(0, 12))
  },[products])
 
  // 跳转详情页
  const navigate = useNavigate()
  const handleToContent = (item) => {
    navigate("/content", {state: {params: { ...item, parentUrl}}})
  }

  // 改变下标
  const handleChangeInd = (ind) => {
    // console.log(ind, products.slice(ind*12, ind*12+12));
    setCurrentList(products.slice(ind*12, ind*12+12))
  }

  return (
    <ListWrapper>
      <div className="list_title">
        <h1 className="list_title_txt">{capitalizeWords(title)}</h1>
        {
          totalPage > 1 && (
            <Pagination 
              totalPage={totalPage} 
              handleChangeInd={ind => handleChangeInd(ind)} 
            />
          )
        }
      </div>
      <ul className="product_list">
        {
          currentList?.map((item, index) => {
            return(
              <li className="product_item" key={index}>
                <Card hoverable={true}>
                  <div className="item_content">
                    <div className="img_box"><img src={require('@/assets/data/' + parentUrl+ '/' + item.url)} alt="" /></div>
                    <div className="text_box">
                      <div className="product_title" onClick={e => handleToContent(item)}>{item.title}</div>
                      <div className="product_detail" dangerouslySetInnerHTML={{__html: item.des}}></div>
                      <div className="add_btn">ADD TO INQUIRY</div>
                    </div>
                  </div>
                </Card>
              </li>
            )
          })
        }
      </ul>
    </ListWrapper>
  )
})

export default ProductList